.shop-page {
	padding: 20px;
}

.product-card {
	background-color: white;
	margin: 10px 0;
	padding: 20px;
	border-radius: 8px;
	box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.product-card h3 {
	margin-bottom: 10px;
}

.product-card p {
	line-height: 1.6;
}

.add-to-cart-btn {
	display: inline-block;
	margin-top: 10px;
	padding: 10px 20px;
	background-color: #333;
	color: white;
	border-radius: 5px;
	cursor: pointer;
}
